article {
  overflow: hidden;
  box-sizing: border-box;
  position: relative;
  width: 60%;
  margin: 0 auto 30px auto;
  padding: 20px 0;
  font-size: 21px;
  color: #ffffff;
  @include block($black, 1, 7px);
  p {
    text-align: left;
    display: block;
    margin: 0 auto;
    width: 80%;
    font-size: 21px;
    color: $white; }
  h1 {
    margin: 20px auto; }
  h2 {
    width: calc(80% - 60px);
    display: inline-block;
    position: relative;
    overflow: hidden;
    padding: 15px 30px;
    margin: 20px auto;
    text-align: center;
    font-size: 1.6em;
    text-align: center;
    @include block(black, .4, 7px);
    &:empty {
      display: none; } }
  h3 {
    width: 80%;
    display: inline-block;
    position: relative;
    overflow: hidden;
    font-size: 24px;
    margin: 0 auto 10px auto; }
  h4 {
    width: 80%;
    display: block;
    margin: 0 auto; }
  h5, h6, pre {
    width: 80%;
    display: block;
    margin: 0 auto; }
  img {
    position: relative;
    height: auto;
    max-width: 100%; }
  a {
    font-size: 21px;
    cursor: pointer;
    color: $red;
    text-decoration: none;
    &:hover {
      text-decoration: underline !important; } }
  ul {
    text-align: left;
    width: 80%;
    margin: 20px auto; }
  ol {
    display: block;
    margin: 10px auto;
    text-align: left;
    width: 80%;
    font-size: 18px; }
  //div:not(.wp-block)
  //display: block
  //margin: 0 auto
  //font-size: 21px
  //img
  //  max-width: calc(100% - (10px + 10px)) !important
  iframe {
    display: block;
    margin: 20px auto;
    width: 100%;
    height: 432px;
    border: none; }
  .more-link {
    text-align: center;
    float: right;
    margin: 25px 0 -20px;
    padding: 16px 20px;
    font-weight: 500;
    width: calc(100% - 40px);
    color: #fff;
    background: $red;
    box-shadow: 0px -3px 50px -5px rgba(255, 0, 0, .3);
    &:hover {
      text-decoration: none !important; } } }

@media screen and (max-width: 1310px) {
  article {
    width: 100%;
    border-radius: 0;
    h2 {
      width: calc(100% - 20px);
      padding: 15px 10px;
      border-radius: 0; }
    h3 {
      width: calc(100% - 20px);
      padding: 0 10px;
      font-size: 30px; } } }

@media screen and (max-width: 500px) {
  article p {
    font-size: 18px; } }
